y: flex;\n  flex-direction: column;\n  width: 98%;\n  height: 98vh;\n  margin: 1vh 1vw;\n}\n\ninput, button, select {\n  background-color: $current-line;\n  color: $foreground;\n  border: $input-outline solid 1px;\n  border-radius: 3px;\n  padding: 0 2px;\n  font-family: 'Roboto Mono', sans-serif;\n\n  &:disabled {\n    background-color: $disabled-input;\n    color: $disabled-input-text;\n    cursor: not-allowed;\n  }\n}\n\nbutton {\n  font-family: 'Roboto', sans-serif;\n\n  &:active {\n    background-color: $highlighted-tree;\n  }\n\n  &:not(:disabled) {\n    cursor: pointer;\n  }\n}\n","// Color scheme\n$tree-background: #282a36\n$outer-background: #21222c\n$semi-background: #242530\n$current-line: #44475a\n$foreground: #f8f8f2\n$comment: #6272a4\n$cyan: #8be9fd\n$green: #50fa7b\n$orange: #ffb86c\n$pink: #ff79c6\n$purple: #bd93f9\n$red: #ff5555\n$yellow: #f1fa8c\n$link-text: #8be9fd\n\n// Additional UI colors\n$highlighted-tree: #52566c\n$input-outline: #14141a\n$disabled-input: #2c2e3b\n$disabled-input-text: #b6b7b6\n$disabled-input-highlighted: #4f5169\n$highlighted-input: #3c4e46\n$highlighted-input-text: $foreground\n\n// Context menu\n$context-menu-border: #ccc\n$context-menu-box-shadow: #888\n$context-menu-background: $current-line\n\n// Value colors\n$value-colour: $green\n$value-type-colour: $yellow\n","#blocker {\n  position: fixed;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  background-color: rgba(0, 0, 0, 0.5);\n  z-index: 999;\n\n  &.visible {\n    display: flex;\n  }\n\n  &:not(.visible) {\n    display: none;\n  }\n}\n","@import '../abstracts/colours';\n\nbutton {\n  background-color: $current-line;\n  color: $foreground;\n  border: $input-outline solid 2px;\n  border-radius: 3px;\n  padding: 2px;\n  font-family: 'Roboto', sans-serif;\n  user-select: none;\n\n  &:disabled {\n    background-color: $disabled-input;\n    color: $disabled-input-text;\n  }\n\n  &:active {\n    background-color: $highlighted-tree;\n  }\n\n  &:not(:disabled) {